<template>
  <div class="columns is-mobile is-centered">
  <form class="column is-narrow mt-5" method="post" v-on:submit.prevent="register">
    <div class="field">
      <div class="control">
    <label>
      Email:
    <input type="email" class="input" name="username" v-model="email">
  </label>
  </div>
  <div class="notification is-danger" v-if="errors.email.length">
    <p v-for="error in errors.email"
      v-bind:key="error"
    >
      {{error}}
  </p>
  </div>
  <div class="notification is-danger" v-if="errors.username.length">
    <p v-for="error in errors.username"
      v-bind:key="error"
    >
      {{error}}
  </p>
  </div>
</div>
<div class="field">
  <div class="control">
    <label>
      Password:
    <input type="password" class="input" name="password" v-model="password">
  </label>
  </div>
  <div class="notification is-danger" v-if="errors.password.length">
    <p v-for="error in errors.password"
      v-bind:key="error"
    >
      {{error}}
  </p>
  </div>
</div>
<div class="field">
  <div class="control">
    <input type="submit" class="button is-primary is-pulled-right" value="Sign Up">
  </div>
</div>
  </form>
</div>
</template>
<script>
export default {
  data() {return {email:"", password: "",
                  errors:{
                    email:[],
                    username:[],
                    password:[],
                  },}
},
  methods:{
    register(){
      let url = "https://tou-dou.herokuapp.com/auth/users/"
      fetch(url,{
        method:"POST",
        headers: {
          'Content-Type': 'application/json',
        },
        body:JSON.stringify({
          email:this.email,
          username:this.email,
          password:this.password,
        })
      })
      .then((response)=>{
        if(response.status!=201){
          return response.json()}
        else{
          this.$router.push("activation-sent")
        }
      }).then(data=>{
        let email_errors=[]
        let username_errors=[]
        let password_errors=[]
        if(data.email){
        data.email.forEach((item, i) => {
          email_errors.push(item)

        });}
        if(data.username){
        data.username.forEach((item, i) => {
          username_errors.push(item)
        });}
        if(data.password){
        data.password.forEach((item, i) => {
          password_errors.push(item)
        });}
        this.errors.email=email_errors
        this.errors.username=username_errors
        this.errors.password=password_errors
      })
  },
},
}
</script>
