<template>

</template>
<script>
export default {
  data() {
    return {}
},
created:function(){
    let url = "https://tou-dou.herokuapp.com/auth/users/activation/"
    fetch(url,{
      method:"POST",
      headers: {
        'Content-Type': 'application/json',
      },
      body:JSON.stringify({
        uid:this.$route.params.uid,
        token:this.$route.params.token,
      })
    })
    .then((response)=>{
        this.$router.push("/")
    })
},
}
</script>
