<template>
  <div class="columns is-mobile is-centered">
  <form class="column is-narrow mt-5" method="post" v-on:submit.prevent="login">
    <div class="field">
      <div class="control">
    <label>
      Email:
    <input type="email" class="input" name="username" v-model="username">
  </label>
  </div>
</div>
<div class="field">
  <div class="control">
    <label>
      Password:
    <input type="password" class="input" name="password" v-model="password">
  </label>
  </div>
</div>
<div class="notification is-danger" v-if="errors.length">
  <p v-for="error in errors"
    v-bind:key="error"
  >
    {{error}}
</p>
</div>
<div class="field">
  <div class="control">
    <input type="submit" class="button is-primary is-pulled-right" value="Login">
  </div>
</div>
  </form>
</div>
</template>
<script>
export default {
  data() {return {username: "",password: "", errors:[]}
},
  methods:{
    login(){
      this.errors=[]
      if(this.username===''){
        this.errors.push('The username is missing!')
      }
      if(this.password===''){
        this.errors.push('The password is missing!')
      }
      if(!this.errors.length){
        let url = "https://tou-dou.herokuapp.com/auth/token/login/"
        fetch(url,{
          method:"POST",
          headers: {
            'Content-Type': 'application/json',
          },
          body:JSON.stringify({
            username:this.username,
            password:this.password,
          })
        })
        .then((response)=>{
            return response.json()
        })
        .then(data=>{
          if(data['auth_token']){
            this.$store.commit('setToken', data['auth_token'])
            this.$router.push("/")
          }
          else if(data.message){
            this.errors.push("Something went wrong. Please try again")
          }
          else{
            for(const property in data){
              this.errors.push(`${property}: ${data[property]}`)
            }
          }
        })
      }
  },
},
}
</script>
